import React, { Component, Fragment } from "react";
import styled from "styled-components";

const WomanWrapper = styled.div`
  width: 100vw;
  padding: 100px;
  outline: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: matrix3d(
    0.8535533905932737,
    0.4999999999999999,
    0.14644660940672619,
    0,
    -0.4999999999999999,
    0.7071067811865476,
    0.4999999999999999,
    0,
    0.14644660940672619,
    -0.4999999999999999,
    0.8535533905932737,
    0,
    22.62994231491119,
    -20.3223304703363,
    101.3700576850888,
    1
  );
  h1 {
    color: white;
  }
`;

const calcX = x => {
  if (typeof window === "undefined") {
    return 0;
  } else {
    // measure centerpoint
    const winWidth = window.innerWidth;
    const center = winWidth / 2;
    const distFromCenter = x - center;
    const decimal = distFromCenter / center;
    console.log({ x, winWidth, center, distFromCenter, decimal });
  }
};

export const WomenList = ({ women, x, y }) => {
  console.log(calcX(x));
  if (women) {
    const allWomen = women.map(woman => {
      return (
        <WomanWrapper skewX={calcX(x)} key={woman.id}>
          <h1>{woman.name}</h1>
        </WomanWrapper>
      );
    });
    return allWomen;
  } else return null;
};
